@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
html, body {
  width: 100vw;
  height: 100vh;
  background-color: black;
  margin: 0;
  font-family: 'Cinzel';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.custom-popover {
  --bs-popover-max-width: 200px;
  --bs-popover-border-color: var(--bs-info) !important;
  --bs-popover-header-bg: var(--bs-black) !important;
  --bs-popover-header-color: var(--bs-info) !important;
  --bs-popover-body-padding-x: 1rem;
  --bs-popover-body-padding-y: .5rem;
  --bs-popover-body-color: var(--bs-info) !important;
  --bs-popover-bg: var(--bs-black) !important;
}
.brand-logo {
  margin-left: 15px;
}
.card .card-content .card-title {
  margin-bottom: 10px;
}
#search-row form {
  padding-top: 20px;
}
.student-name {
  position: relative;
  top: -5px;
}
.collection-item form {
  margin-top: -5px;
}
#button {
  display: none;
}
#tooltip {
  background: #333;
  color: white;
  font-weight: bold;
  padding: 4px 8px;
  font-size: 13px;
  border-radius: 4px;
  display: none;
}
#tooltip[data-show] {
  display: block;
}
#arrow,
#arrow::before {
  position: absolute;
  width: 8px;
  height: 8px;
  background: inherit;
}
#arrow {
  visibility: hidden;
}
#arrow::before {
  visibility: visible;
  content: '';
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
#tooltip[data-popper-placement^='top'] > #arrow {
  bottom: -4px;
}
#tooltip[data-popper-placement^='bottom'] > #arrow {
  top: -4px;
}
#tooltip[data-popper-placement^='left'] > #arrow {
  right: -4px;
}
#tooltip[data-popper-placement^='right'] > #arrow {
  left: -4px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#nav-pic {
    max-width: 6.5vw;
    max-height: 6.5vh;
}
#user-pic {
    border-radius: 5px;
    max-width: 30vw;
    max-height: 30vh;
}


#monster-image {
    border-radius: 5%;
    border: 2px solid black;
    max-width: 50vw;
    max-height: 50vh;
}
